import template from './sw-cms-el-config-zen-features.html.twig';

const { Component, Mixin } = Shopware;

Component.register('sw-cms-el-config-zen-features', {
    template,

    mixins: [
        Mixin.getByName('cms-element')
    ],

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('zenit-features');
        }
    }
});
