import template from './zen-theme-index.html.twig';
import './zen-theme-index.scss';
import themeJson from '../../../../../../../theme.json'
import composerJson from '../../../../../../../../../composer.json'

const { Component, Mixin } = Shopware;
const { Criteria } = Shopware.Data;

Component.register('zen-theme-stratus-index', {
    template,

    inject: [
        'repositoryFactory'
    ],

    mixins: [
        Mixin.getByName('notification'),
    ],

    data() {
        return {
            plugin: {},
            themes: null,
            duplicateThemes: null,
            pluginJson: composerJson,
            themeJson: themeJson,
            isLoading: true,
            orderedChangelog: {}
        };
    },

    metaInfo() {
        return {
            title: this.$createTitle()
        };
    },

    computed: {
        pluginRepository() {
            return this.repositoryFactory.create('plugin');
        },

        themeRepository() {
            return this.repositoryFactory.create('theme');
        },

        hasSalesChannel() {
            return this.getSalesChannel();
        },

        hasDuplicateThemeSalesChannel() {
            return this.getDuplicateThemeSalesChannel();
        },
    },

    filters: {
        reverse: function (value) {
            return value.slice().reverse()
        }
    },

    created() {
        this.getPlugin();
        this.getTheme();
    },

    methods: {
        getPlugin() {
            const pluginClass = this.pluginJson['extra']['shopware-plugin-class'];
            const pluginName = pluginClass.slice(pluginClass.lastIndexOf("\\") + 1);

            this.isLoading = true;

            const pluginCriteria = new Criteria();
            pluginCriteria.addFilter(Criteria.equals('plugin.name', pluginName));

            this.pluginRepository.search(pluginCriteria, Shopware.Context.api).then((searchResult) => {
                this.plugin = searchResult.first();

                if (this.plugin.translated.changelog) {
                    this.getOrderedChangelog(this.plugin.translated.changelog);
                }

                this.isLoading = false;
            })
        },

        getTheme() {
            this.isLoading = true;

            const criteria = new Criteria();
            criteria.addAssociation('previewMedia');
            criteria.addAssociation('salesChannels');
            criteria.addFilter(Criteria.contains('name', this.themeJson.name));
            criteria.addFilter(Criteria.equals('author', this.themeJson.author));

            this.themeRepository.search(criteria, Shopware.Context.api).then((searchResult) => {
                this.themes = searchResult;
                this.isLoading = false;

                if (this.themes) {
                    const ids = this.themes.map(theme => theme.id);
                    const technicalNames = this.themes.map(theme => theme.technicalName);

                    this.getDuplicateTheme(ids);
                    this.updateInfo(technicalNames);
                }

            });
        },

        getDuplicateTheme(ids) {
            this.isLoading = true;

            if (ids <= 0) {
                return false;
            }

            const criteria = new Criteria();
            criteria.addAssociation('previewMedia');
            criteria.addAssociation('salesChannels');
            criteria.addFilter(Criteria.equalsAny('parentThemeId', ids));

            this.themeRepository.search(criteria, Shopware.Context.api).then((searchResult) => {
                this.duplicateThemes = searchResult;

                this.isLoading = false;
            });
        },

        getSalesChannel() {
            if (this.themes) {
                const arrayLength = this.themes.length;
                for (let i = 0; i < arrayLength; i++) {
                    if (this.themes[i].salesChannels.length) {
                        return true;
                    }
                }
            }
        },

        getDuplicateThemeSalesChannel() {
            if (this.duplicateThemes) {
                const arrayLength = this.duplicateThemes.length;

                for (let i = 0; i < arrayLength; i++) {
                    if (this.duplicateThemes[i].salesChannels.length) {
                        return true;
                    }
                }
            }
        },

        getOrderedChangelog(value) {
            const reversedKeys = Object.keys(value).reverse();

            reversedKeys.forEach(key => {
                this.orderedChangelog[key] = value[key];
            })
        },

        openThemeConfig(index) {
            if (!this.themes) {
                this.$router.push({ name: 'sw.theme.manager.index' });
            } else {
                this.$router.push({ name: 'sw.theme.manager.detail', params: { id: this.themes[index].id } });
            }
        },

        openDuplicateThemeConfig(index) {
            if (!this.duplicatethemes) {
                this.$router.push({ name: 'sw.theme.manager.index' });
            } else {
                this.$router.push({ name: 'sw.theme.manager.detail', params: { id: this.duplicatethemes[index].id } });
            }
        },

        openPluginSettings() {
            this.$router.push({ name: 'sw.plugin.settings', params: { namespace: this.pluginName } });
        },

        updateInfo(technicalNames) {
            if (technicalNames.some(res => res.includes('zenitPlatformStratusSet'))) {
                const childThemeVersion = '2.10.0';
                const childThemePriority = 'info';
                const notificationStorageKey = 'zenit-child-theme-update-available-notification'
                const notificationStorageValue = localStorage.getItem(notificationStorageKey);
                const updatePriorityText = this.$tc(`zen-theme-stratus.general.updateAvailableChildThemePriority.${childThemePriority}`);

                if (notificationStorageValue !== childThemeVersion) {
                    this.createNotificationInfo({
                        title: this.$tc('zen-theme-stratus.general.updateAvailableChildThemeTitle', 0, {updatePriority: updatePriorityText}),
                        message: this.$tc('zen-theme-stratus.general.updateAvailableChildThemeMessage', 0, {updateVersion: childThemeVersion}),
                        variant: childThemePriority,
                        system: true,
                        autoClose: false,
                    });

                    localStorage.setItem(notificationStorageKey, childThemeVersion)
                }
            }
        },
    }
});
