const {Mixin} = Shopware;

Shopware.Component.override('sw-extension-card-base', {

    mixins: [
        Mixin.getByName('notification'),
    ],

    created() {
        this.updateNotification();
    },

    methods: {
        updateNotification() {
            if (this.extension.name.includes("zenitPlatformStratusSet")) {
                const childThemeVersion = '2.10.0';
                const childThemePriority = 'info';
                const notificationStorageKey = 'zenit-child-theme-update-available-notification'
                const notificationStorageValue = localStorage.getItem(notificationStorageKey);
                const updatePriorityText = this.$tc(`zen-theme-stratus.extension.general.updateAvailableChildThemePriority.${childThemePriority}`);

                if (this.extension.version !== childThemeVersion) {
                    if (notificationStorageValue !== childThemeVersion) {
                        this.createNotificationInfo({
                            title: this.$tc('zen-theme-stratus.extension.general.updateAvailableChildThemeTitle', 0, {updatePriority: updatePriorityText}),
                            message: this.$tc('zen-theme-stratus.extension.general.updateAvailableChildThemeMessage', 0, {updateVersion: childThemeVersion}),
                            variant: childThemePriority,
                            system: true,
                            autoClose: false,
                        });

                        localStorage.setItem(notificationStorageKey, childThemeVersion)
                    }
                }
            }
        }
    },
});
