import template from './sw-cms-el-zen-features.html.twig';
import './sw-cms-el-zen-features.scss';

const {Component} = Shopware;

Component.register('sw-cms-el-zen-features', {
    template,

    mixins: [
        'cms-element'
    ],

    computed: {
    },

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.initElementConfig('zenit-features');
        }
    }
});
