import './component';
import './config';
import './preview';

Shopware.Service('cmsService').registerCmsElement({
    name: 'zen-image-slider',
    label: 'sw-cms.elements.zenImageSlider.label',
    component: 'sw-cms-el-zen-image-slider',
    configComponent: 'sw-cms-el-config-zen-image-slider',
    previewComponent: 'sw-cms-el-preview-zen-image-slider',
    defaultConfig: {
        sliderItems: {
            source: 'static',
            value: [],
            required: true,
            entity: {
                name: 'media',
            },
        },
        navigationArrows: {
            source: 'static',
            value: 'inside',
        },
        navigationDots: {
            source: 'static',
            value: null,
        },
        displayMode: {
            source: 'static',
            value: 'standard',
        },
        minHeight: {
            source: 'static',
            value: '300px',
        },
        verticalAlign: {
            source: 'static',
            value: null,
        },
        customClasses: {
            source: 'static',
            value: null,
        },
        autoplay: {
            source: 'static',
            value: false,
        },
        autoplayTimeout: {
            source: 'static',
            value: 5000,
        },
        autoplayHoverPause: {
            source: 'static',
            value: true,
        },
        speed: {
            source: 'static',
            value: 500,
        },
        mode: {
            source: 'static',
            value: 'carousel',
        },
        axis: {
            source: 'static',
            value: 'horizontal',
        },
        items: {
            source: 'static',
            value: 1,
        },
        gutter: {
            source: 'static',
            value: 0,
        }
    },
    enrich: function enrich(elem, data) {
        if (Object.keys(data).length < 1) {
            return;
        }

        Object.keys(elem.config).forEach((configKey) => {
            const entity = elem.config[configKey].entity;

            if (!entity) {
                return;
            }

            const entityKey = entity.name;
            if (!data[`entity-${entityKey}`]) {
                return;
            }

            elem.data[configKey] = [];
            elem.config[configKey].value.forEach((sliderItem) => {
                elem.data[configKey].push({
                    newTab: sliderItem.newTab,
                    url: sliderItem.url,
                    media: data[`entity-${entityKey}`].get(sliderItem.mediaId),
                    text: sliderItem.text,
                    color: sliderItem.color,
                    backgroundColor: sliderItem.backgroundColor,
                    textBorderRadius: sliderItem.textBorderRadius,
                    textBorderRadiusTopLeft: sliderItem.textBorderRadiusTopLeft,
                    textBorderRadiusTopRight: sliderItem.textBorderRadiusTopRight,
                    textBorderRadiusBottomRight: sliderItem.textBorderRadiusBottomRight,
                    textBorderRadiusBottomLeft: sliderItem.textBorderRadiusBottomLeft,
                    textPaddings: sliderItem.textPaddings,
                    textPaddingTop: sliderItem.textPaddingTop,
                    textPaddingRight: sliderItem.textPaddingRight,
                    textPaddingBottom: sliderItem.textPaddingBottom,
                    textPaddingLeft: sliderItem.textPaddingLeft,
                    textMargins: sliderItem.textMargins,
                    textMarginTop: sliderItem.textMarginTop,
                    textMarginRight: sliderItem.textMarginRight,
                    textMarginBottom: sliderItem.textMarginBottom,
                    textMarginLeft: sliderItem.textMarginLeft,
                    verticalTextAlign: sliderItem.verticalTextAlign,
                    horizontalTextAlign: sliderItem.horizontalTextAlign,
                    textMinWidth: sliderItem.textMinWidth,
                    textMaxWidth: sliderItem.textMaxWidth,
                    overlay: sliderItem.overlay,
                    overlayColor: sliderItem.overlayColor,
                    overlayOpacity: sliderItem.overlayOpacity,
                    animationIn: sliderItem.animationIn,
                    animation: sliderItem.animation,
                    customItemClasses: sliderItem.customItemClasses,
                });
            });
        });
    },
});
